}

/* Estilo para el botón */
.Rectangle1 {
  width: 400.5px;
  height: 51px;
  margin: 25px auto; /* Centrado horizontalmente */
  padding: 10px 22.3px 17.4px 18.1px;
  border-radius: 11px;
  background-color: #237bbb;
  color: #fff; /* Color del texto */
  font-family: "Nunito", sans-serif;
  font-size: 20px;
  font-weight: bold;
  font-style: italic;
  text-align: center;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
  display: block; /* Para asegurar el centrado dentro del contenedor */
}

/* Efecto hover para el botón */
.Rectangle1:hover {
  background-color: #004C84; /* Cambia el color cuando se pasa el cursor */
}

.Rectangle-63-primary {
  width: 400.5px;
  height: 51px;
  margin: 25px auto; /* Centrado horizontalmente */
  padding: 10px 22.3px 17.4px 18.1px;
  border-radius: 11px;
  background-color: #237bbb;
  color: #fff;
  font-family: "Nunito", sans-serif;
  font-size: 20px;
  font-weight: bold;
  font-style: italic;
  text-align: center;
