}

/* Segundo botón con borde */
.Rectangle2 {
  width: 400.5px;
  height: 51px;
  margin: 25px auto 153px; /* Centrado horizontalmente y espacio inferior */
  padding: 10px 22.3px 17.4px 18.1px;
