* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Estilos del header */
.header {
  display: flex;
  align-items: center;
  padding: 20px;
  background-color: #ffffff;
}

/* Estilos del logo */
.logo img {
  width: 350px;
  height: 50px;
  object-fit: contain;
}

/* Divisor vertical entre el logo y los botones */
.divider {
  width: 2px; /* Línea vertical */
  height: 40px; /* Altura de la línea */
  background-color: #004C84; /* Color de la línea */
  margin: 0 20px; /* Espaciado horizontal alrededor del divisor */
}

/* Contenedor de los botones de navegación */
.nav-buttons {
  display: flex;
  justify-content: space-around; /* Distribuye espacio de forma uniforme */
  flex-grow: 1; /* Hace que los botones usen el espacio disponible en el header */
}

/* Estilos de los botones de navegación */
.nav-button {
  font-family: "Nunito", sans-serif;
  font-size: 20px;
  font-weight: bold;
  font-style: italic;
  color: #237BBB;
  text-decoration: none;
  position: relative;
  transition: color 0.3s ease;
  white-space: nowrap; /* Evita que los botones se dividan en varias líneas */
}

/* Efecto al pasar el mouse sobre los botones */
.nav-button:hover {
  color: #004C84;
}

/* Subrayado al pasar el mouse */
.nav-button::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 2px;
  background-color: transparent;
  border-radius: 11px;
  transition: background-color 0.3s ease;
}

.nav-button:hover::after {
  background-color: #004C84;
}

